.homeContainer{
  height: 100vh;
  background-size: cover;
  background-position: center;
}

.titleContainer{
  position: absolute;
  top: 54vh;
  left: 30vh;
}

.titleContainer h1{
  color: #EFF0EB;
  font-size: 4rem;
  font-family: 'Berlin Sans FB Demi';
}

.subTitleContainer{
  position: absolute;
  top: 35vh;
  left: 45vh;
}

.subTitleContainer h2{
  color: #173657;
  font-size: 3rem;
}

@media screen and (max-width: 600px){
  .homeContainer{
    height: 100vh;
    background-size: cover;
    background-position: right;
  }
}
