.trombiContainer{
  min-height: 100vh;
  background-color: #173657;
}

.trombiSubContainer{
  padding-top: 5vh;
  padding-left: 1vw;
  padding-right: 5vw;
  margin-top: 20vh;
  font-weight: bold;
  color: #EFF0EB;
  max-width: 100vw;
  flex: 1;
  height: 70vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow: scroll;
}

.headerBackground{
  position: fixed;
  top:0;
  z-index: 10;
  height: 20vh;
  width: 100%;
  background-size: cover;
  background-position: center;
}

@media screen and (max-width: 600px){
  .headerBackground{
    position: fixed;
    top: 0;
    z-index: 10;
    height: 10vh;
    width: 100%;
    background-size: cover;
    background-position: center;
  }

  .trombiSubContainer{
    padding-top: 15vh;
    padding-left: 5vw;
    padding-right: 5vw;
    padding-bottom: 4vh;
    height: 100vh;
    margin-top: 0vh;
    font-weight: bold;
    color: #EFF0EB;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: scroll;
  }
}
