@import url(//db.onlinewebfonts.com/c/b6539b6d3432c623d8d4f9cc2a29589e?family=Berlin+Sans+FB+Demi);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {font-family: "Berlin Sans FB Demi"; src: url("//db.onlinewebfonts.com/t/b6539b6d3432c623d8d4f9cc2a29589e.eot"); src: url("//db.onlinewebfonts.com/t/b6539b6d3432c623d8d4f9cc2a29589e.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/b6539b6d3432c623d8d4f9cc2a29589e.woff2") format("woff2"), url("//db.onlinewebfonts.com/t/b6539b6d3432c623d8d4f9cc2a29589e.woff") format("woff"), url("//db.onlinewebfonts.com/t/b6539b6d3432c623d8d4f9cc2a29589e.ttf") format("truetype"), url("//db.onlinewebfonts.com/t/b6539b6d3432c623d8d4f9cc2a29589e.svg#Berlin Sans FB Demi") format("svg"); }

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a{
  font-family: 'Berlin Sans FB Demi', source-code-pro;
}

.homeContainer{
  height: 100vh;
  background-size: cover;
  background-position: center;
}

.titleContainer{
  position: absolute;
  top: 54vh;
  left: 30vh;
}

.titleContainer h1{
  color: #EFF0EB;
  font-size: 4rem;
  font-family: 'Berlin Sans FB Demi';
}

.subTitleContainer{
  position: absolute;
  top: 35vh;
  left: 45vh;
}

.subTitleContainer h2{
  color: #173657;
  font-size: 3rem;
}

@media screen and (max-width: 600px){
  .homeContainer{
    height: 100vh;
    background-size: cover;
    background-position: right;
  }
}

.headerContainer{
  top: 0;
  z-index: 15;
  position: fixed;
  height: 10vh;
  width: 100%;
  display: flex;
  text-align: center;
  justify-content: space-around;
  align-items: center;
}

.headerContainer a{
  text-decoration: none;
  font-weight: bold;
  font-size: 4vw;;
  color: #EFF0EB;
}

.headerContainer a:hover{
  color: #173657;
}

.trombiContainer{
  min-height: 100vh;
  background-color: #173657;
}

.trombiSubContainer{
  padding-top: 5vh;
  padding-left: 1vw;
  padding-right: 5vw;
  margin-top: 20vh;
  font-weight: bold;
  color: #EFF0EB;
  max-width: 100vw;
  flex: 1 1;
  height: 70vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow: scroll;
}

.headerBackground{
  position: fixed;
  top:0;
  z-index: 10;
  height: 20vh;
  width: 100%;
  background-size: cover;
  background-position: center;
}

@media screen and (max-width: 600px){
  .headerBackground{
    position: fixed;
    top: 0;
    z-index: 10;
    height: 10vh;
    width: 100%;
    background-size: cover;
    background-position: center;
  }

  .trombiSubContainer{
    padding-top: 15vh;
    padding-left: 5vw;
    padding-right: 5vw;
    padding-bottom: 4vh;
    height: 100vh;
    margin-top: 0vh;
    font-weight: bold;
    color: #EFF0EB;
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: scroll;
  }
}

.footerContainer{
  position: fixed;
  bottom: 0;
  width: 100%;
}

.photoContainer{
  padding: 20px;
  text-align: center;
  height: 275px;
  width: 250px;
}

.image{
  height: 200px;
  width: 200px;
}

.photoContainer h3{
  font-family: 'Berlin Sans FB Demi';
  color: #B2CAEE;
}

.photoContainer h4{
  color: #F6966E
}

.programmeContainer{
  min-height: 100vh;
  background-color: #173657;
}

.headerBackground{
  position: fixed;
  top:0;
  z-index: 10;
  height: 20vh;
  width: 100%;
  background-size: cover;
  background-position: center;
}

.programmeSubContainer{
  padding-top: 5vh;
  padding-left: 5vw;
  margin-top: 20vh;
  font-weight: bold;
  color: #EFF0EB;
  text-align: justify;
  padding-right: 5vw;
}

@media screen and (max-width: 600px){
  .headerBackground{
    position: fixed;
    top:0;
    z-index: 10;
    height: 10vh;
    width: 100%;
    background-size: cover;
    background-position: center;
  }

  .programmeSubContainer{
    padding-top: 5vh;
    padding-left: 5vw;
    padding-bottom: 15vh;
    margin-top: 10vh;
    font-weight: bold;
    color: #EFF0EB;
    text-align: justify;
    padding-right: 5vw;
  }
}

.contactContainer{
  height: 100vh;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contactSubContainer{
  padding-top: 35vh;
  text-align: center;
}

.imgContact{
  width: 10vh;
  border-radius: 2vh;
  margin: 2vh;
}

.imgMail{
  width: 10vh;
  margin: 2vh;
}

.contactContainer h3{
  color: #EFF0EB;
  margin-top: 1vh;
  font-family: 'Berlin Sans FB Demi', source-code-pro;
}

.errorContainer{
  height: 100vh;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.errorContainer h1{
  padding-top: 5vh;
  color: #EFF0EB;
  font-family: 'Berlin Sans FB Demi';
}

