.contactContainer{
  height: 100vh;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contactSubContainer{
  padding-top: 35vh;
  text-align: center;
}

.imgContact{
  width: 10vh;
  border-radius: 2vh;
  margin: 2vh;
}

.imgMail{
  width: 10vh;
  margin: 2vh;
}

.contactContainer h3{
  color: #EFF0EB;
  margin-top: 1vh;
  font-family: 'Berlin Sans FB Demi', source-code-pro;
}
